/*

#222831 #393E46 #00ADB5 #EEEEEE

font-family: 'Plus Jakarta Sans', sans-serif;
font-weight: 400; 800;
font-family: 'Merriweather', serif;
font-weight: 900;

*/

body {
  background-color: #222831;
  color: #eeeeee;
  font-family: "Plus Jakarta Sans", sans-serif;
  font-weight: 400;
  height: 100vh;
  margin: 0;
  padding-left: 2rem;
}

h1 {
  font-family: "Merriweather", serif;
  font-weight: 900;
  font-size: 2.5rem;
  color: #00adb5;
  margin-top: 0;
  padding-top: 3rem;
}

h2 {
  font-family: "Merriweather", serif;
  font-weight: 900;
  font-size: 1.5625rem;
  color: #00adb5;
}

/* Converter styles */
.converter {
  margin: 3rem;
  display: inline-block;
}
.converter input {
  width: 7rem;
}
.conv-output {
  padding: 0.5rem 0 0;
}
.conv-output button {
  background-color: #393e46;
  border: 0;
  border-radius: 0.625rem;
  font-family: "Plus Jakarta Sans", sans-serif;
  font-weight: 400;
  margin: 0 0 0 0.5rem;
}
.conv-output .copy-conf {
  color: #393e46;
  font-family: "Plus Jakarta Sans", sans-serif;
  font-weight: 400;
  font-size: 1rem;
  margin: 0 0 0 0.5rem;
}